import React from "react";
import { Link } from "react-router-dom";
import { Panel, PanelHeader } from "../../../components/panel/panel.jsx";
import {
  ShowModalDataKadar,
  showModal,
  getDataKadar,
  hideModal,
} from "../../../actions/datamaster_action.jsx";
import { connect } from "react-redux";
import Swal from "sweetalert2";
import {
  NotifError,
  ToastNotification,
} from "../../../components/helpers/notification.jsx";
import { reset } from "redux-form";
import { deleteData, postData, putData } from "../../../config/axios.jsx";
import Tabel from "../../../components/helpers/tabel.jsx";
import FormModalKadar from "./FormModalKadar.jsx";
import ModalGlobal from "../../../components/helpers/ModalGlobal.jsx";

const maptostate = (state) => {
  return {
    // DataGroup: state.datamaster.GetDataGroup,
    DataKadar: state.datamaster.getDataKadar,
    hideModal: state.datamaster.modalDialog,
  };
};

class MasterKadar extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isEdit: false,
      modalDialog: false,
      isLoading: false,
      columns: [
        {
          dataField: "kadar",
          text: "Kadar",
        },
        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row) => {
            let dataEdit = {
              _id: row._id,
              kadar: row.kadar,
            };
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    onClick={() => this.formEditKadar(dataEdit)}
                    className="btn btn-primary mr-3"
                  >
                    {" "}
                    <i className="fa fa-edit"></i>
                  </button>
                  <button
                    onClick={() =>
                      this.hapusDataKadar(
                        row.kadar,
                        this.props.dispatch
                      )
                    }
                    className="btn btn-danger"
                  >
                    <i className="fa fa-trash"></i>
                  </button>
                </div>
              </div>
            );
          },
        },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  hapusDataKadar = (params) => {
    Swal.fire({
      html:
        "Apakah Anda Yakin Ingin " +
        "Menghapus " +
        "<h1><b>Kadar " +
        params +
        "</b> ?</h1>",
      icon: "warning",
      position: "top-center",
      cancelButtonText: "Tidak",
      showCancelButton: true,
      confirmButtonText: "OK",
      showConfirmButton: true,
    }).then((result) => {
      if (result.isConfirmed) {
        deleteData("masterkadar/delete/by-kadar/" + params).then(() => {
          ToastNotification("success", "Data Berhasil Di Hapus").then(() =>
            this.props.dispatch(getDataKadar())
          );
        }).catch((err) => {
          ToastNotification("info",err.response?.data)
        })
      }
    });
  };
  componentDidMount() {
    this.props.dispatch(getDataKadar());
  }
  handleSubmit(data) {
    this.setState({ isLoading: true });
    let data_post = {
      kadar: data.kadar,
    };
    this.state.isEdit
      ? putData("masterkadar/edit/by-kadar/" + data.kadar_lama, data_post)
          .then(() => {
            this.props.dispatch(hideModal());
          })
          .then(() => ToastNotification("success", "Data Berhasil Diedit"))
          .then(() => {
            this.props.dispatch(getDataKadar());
          })
          .then(() => {
            this.setState({ isLoading: false });
          })
          .then(() => {
            this.props.dispatch(reset("ModalKadar"));
          })
        .catch((error) => {
          ToastNotification("info", error.response?.data)
          this.setState({ isLoading: false });
          })
      : postData("masterkadar/add", data_post)
          .then(() => {
            this.props.dispatch(hideModal());
          })
          .then(() => ToastNotification("success", "Data Berhasil Disimpan"))
          .then(() => {
            this.props.dispatch(getDataKadar());
          })
          .then(() => {
            this.props.dispatch(reset("ModalKadar"));
          })
          .then(() => {
            this.setState({ isLoading: false });
          })
          .catch((error) => this.ErrorHandling(error));
  }
  ErrorHandling(err) {
    this.setState({ isLoading: false });
    NotifError(err);
  }
  formEditKadar(dataEdit) {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowModalDataKadar(dataEdit));
    this.setState({
      isEdit: true,
    });
  }
  ShowModalDataKadar() {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowModalDataKadar(false));
    this.setState({
      isEdit: false,
    });
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Data Master</Link>
          </li>
          <li className="breadcrumb-item active">Master Kadar</li>
        </ol>
        <h1 className="page-header">Master Kadar </h1>
        <Panel>
          <PanelHeader>Master Kadar</PanelHeader>
          <br />

          {/* Master Kadar */}
          <div className="container">
            <Tabel
              keyField="kondisi"
              tambahData={true}
              handleClick={() => this.ShowModalDataKadar()}
              exportCsv={true}
              data={this.props.DataKadar}
              columns={this.state.columns}
              empty={this.props.DataKadar}
              textEmpty="Data Kadar Kosong"
            />
          </div>
          <br />
          {/* End Master Kadar */}

          <ModalGlobal
            title={this.state.isEdit ? "Edit Data Kadar" : "Tambah Data Kadar"}
            size="P"
            content={
              <FormModalKadar
                isLoading={this.state.isLoading}
                isEdit={this.state.isEdit}
                onSubmit={(data) => this.handleSubmit(data)}
              />
            }
          />

          {/* End Tambah Master Kadar  */}
        </Panel>
      </div>
    );
  }
}

export default connect(maptostate, null)(MasterKadar);
